<template>
  <div class="outBox">
    <h1>상품리스트</h1>
    <div class="p1">
      <div v-for="item in lists" :key="item.id" class="listBox">
        <img :src="item.image" :alt="item.name" class="listImg" />
        <h3>{{ item.name }}</h3>
        <p>가격 : {{ item.price }}만원</p>
        <div class="countBox">
          <button @click.stop="mcount(item)"> - </button>
          <span>{{ item.count }}</span>
          <button @click.stop="acount(item)"> + </button>
        </div>
        <button class="cartbtn" @click.stop="addToCart(item)">장바구니 추가</button>
      </div>
    </div>       
  </div>    
</template>

<script setup>
import { ref, toRefs } from 'vue';
import noteImage from "@/assets/image/notebook.jpg";
import nImage from "@/assets/image/ref.jpg";
import hImage from "@/assets/image/mobile.png";

// props를 객체 형태로 선언
const props = defineProps({
  addToCart: {
    type: Function,
    required: true
  }
});

// toRefs를 사용해 안전하게 구조 분해 (template에서는 자동 언래핑됨)
const { addToCart } = toRefs(props);

const lists = ref([
  { id: 1, name: '노트북', price: 120, image: noteImage, count: 1 },
  { id: 2, name: '냉장고', price: 200, image: nImage, count: 1 },
  { id: 3, name: '핸드폰', price: 150, image: hImage, count: 1 },
]);

const mcount = (item) => {
  if (item.count > 1) item.count--;
};

const acount = (item) => {
  item.count++;
};
</script>

<style scoped>
.outBox {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.outBox h1 {
  text-align: center;
  margin-bottom: 20px;
}

.p1 {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.listBox {
  flex: 1 1 250px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.listImg {
  width: 100%;
  height: auto;
  margin-bottom: 12px;
  border-radius: 4px;
}

.listBox h3 {
  margin: 10px 0;
  font-size: 1.2em;
}

.listBox p {
  margin: 8px 0;
  font-size: 1em;
  color: #555;
}

.countBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 12px 0;
}

.countBox button {
  width: 30px;
  height: 30px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  cursor: pointer;
  outline: none;
}

.countBox span {
  margin: 0 10px;
  font-size: 1em;
}

.cartbtn {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cartbtn:hover {
  background-color: #218838;
}
</style>
