<!-- AppHeader.vue -->
<template>
    <header>
      <h1>My Shop</h1>
      <nav>
        <router-link to="/">Home</router-link>
        <router-link to="/product">상품</router-link>
        <router-link to="/cart">장바구니({{ cart.length }})</router-link>
        <router-link to="/checkout">결제</router-link>
        <router-link to="/contact">문의</router-link>
      </nav>
    </header>
  </template>
  
  <script setup>
  import { toRefs } from 'vue';
  
  // defineProps에서 객체 형태로 선언한 후, toRefs를 사용해 구조 분해합니다.
  const props = defineProps({
    cart: {
      type: Array,
      default: () => []
    }
  });
  const { cart } = toRefs(props);
  </script>
  