<template>
    <div>
      <h1>장바구니</h1>
      <h3 v-if="cart.length === 0">장바구니가 비었습니다.</h3>
      <div v-else>
        <div v-for="item in cart" :key="item.id" class="cart-list">
          <img :src="item.image" :alt="item.name" />
          <h3>{{ item.name }}</h3>
          <h2> 가격 : {{ item.price }} 만원</h2>
          <h2> 수량 : {{ item.count }} 개</h2>
          <button @click="removeFromCart(item.id)">삭제</button>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
import { toRefs } from 'vue';

const props = defineProps({
  cart: {
    type: Array,
    default: () => []
  },
  removeFromCart: {
    type: Function,
    required: true
  }
});
const { cart, removeFromCart } = toRefs(props);
</script>
  